import axios from 'axios';
import { AnyAction } from 'redux';
import { ThunkAction } from 'redux-thunk';
import { IAppState } from '.';
import {
	setBrands,
	setCampaigns,
	setCurrentUser,
	setUserProfile,
} from './action-creators';

export function initializeState(): ThunkAction<
	Promise<any>,
	IAppState,
	{},
	AnyAction
> {
	return async (dispatch, getState) => {
		return new Promise(async (res, rej) => {
			try {
				//TODO: Add all fetches
				await dispatch(fetchLoggedInUser());
				//await dispatch(fetchUserProfile());
				// await dispatch(fetchUserBrands());
				// await dispatch(fetchUserCampaigns());
				res(true);
			} catch (error) {
				console.log(error);
				res(false);
			}
		});
	};
}

export function fetchLoggedInUser(): ThunkAction<
	Promise<any>,
	IAppState,
	{},
	AnyAction
> {
	return async (dispatch, getState) => {
		return new Promise(async (res, rej) => {
			try {
				const token = localStorage.getItem('token');
				if(token) {
					const response = await axios.get(process.env.REACT_APP_BACKEND_URL+'/api/user/info',{headers:{'Authorization':token}}) 
					const user = response.data.user
					Moengage.add_first_name(user.name);
          			Moengage.add_email(user.email);
          			Moengage.add_mobile(user.phone);
          			Moengage.add_user_name(user.name); 
          			Moengage.add_unique_user_id(user._id);
          			Moengage.add_user_attribute("role",user.role);
					Moengage.add_user_attribute("image",user.imgUrl);
          			Moengage.track_event("WobbLogin", user);
					dispatch(setCurrentUser(user));
				res(true);
				} else {
					res(false);
				}
				
			} catch (error) {
				res(false);
			}
		});
	};
}


